
.about .section-title{
	padding-top :20px;
	margin-top : 15px;
	}
.about-grid, .about-img, .about-grid .section-title .text {
	max-width : 600px;
	margin-left : auto;
	margin-right : auto;

}	

.about-item-icon {
	margin-right :24px;
}

.about-item-icon img {
	width: 40px;
}

.about-item-text h3{
	margin-bottom : 0;
	font-weight: 500;
	
}

.about-item-text {
	font-weight : 400;
}

.about-grid:nth-child(2){
	padding : 50px 0;
	row-gap: 20px;
	grid-template-columns: repeat(2,1fr);
}
.about-video {
	position : relative;
}

.about-video video {
	width : 100%;
}
.vidplaybtn {
	position : absolute;
	left : 50%;
	top :50%;
	transform : translate (-50%,-50%);
	width : 60px;
	height : 60px;
	border-radius :50%;
	background-color: rgba(255,255,255,0.8);

}

@media screen and (min-width: 992px){
	.about-grid:nth-child(1),
	.about-grid:nth-child(2),
	.about-grid:nth-child(3)
		{
		max-width : 100%;
		}
		
	.about-grid:nth-child(1),
	.about-grid:nth-child(3){
		grid-template-columns : repeat (2,1fr);
		column-gap : 80px;
		}
	.about-grid:nth-child(1) .section-title {
		text-align : left;
	}	
	.about-grid:nth-child(3) .section-title {
		text-align: right;
	}

	.about-grid:nth-child(3)  {
		margin-top:40px;
	}

	.about-grid:nth-child(2){
		grid-template-columns : repeat(4,1fr);
		margin-top :30px;
		column-gap: 20px;
	}
}
